@import 'reset';
@import 'mixin';

@font-face {
	font-family: 'HelveticaNeue';
	src: url('../font/HelveticaNeue.eot?#iefix') format('embedded-opentype'),  url('../font/HelveticaNeue.woff') format('woff'), url('../font/HelveticaNeue.ttf')  format('truetype'), url('../font/HelveticaNeue.svg#HelveticaNeue') format('svg');
	font-weight: normal;
	font-style: normal;
}

html, body {
	width: 100%;
	height: 100%;
	font-family: "HelveticaNueu",sans-serif;
	color: #fff;
	overflow: hidden;
}
body {
	background-image: url('../img/wallpaper.png');
	background-repeat: no-repeat;
	background-position: center center;
	background-attachment: scroll;
	background-clip: border-box;
	background-origin: border-box;
	background-size: cover;
	main {
		background-color: rgba(0,0,0,.5);
		display: -webkit-box;
		display: -moz-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
		align-items: center;
		justify-content: center;
		// flex-direction: column;
		width: 100%;
		height: 100%;
		overflow: scroll;
		section {
			padding: 2em;
			margin: 2em;
			border-radius: .2em;
			box-shadow: rgba(0,0,0,.5) 0 2px 32px;
			position: relative;
			overflow: hidden;
			box-sizing: border-box;
			z-index: 10;
			background-color: rgba(255,255,255,.8);
			&:before {
				content: '';
				position: absolute;
				width: 110%;
				height: 110%;
				top: -5%;
				left: -5%;
				background-image: url('../img/wallpaper.png');
				background-repeat: no-repeat;
				background-size: cover;
				background-attachment: fixed;
				background-position: center center;
				-webkit-filter: blur(12px) contrast(120%) opacity(.8);
				-moz-filter: blur(12px) contrast(120%) opacity(.8);
				-ms-filter: blur(12px) contrast(120%) opacity(.8);
				-o-filter: blur(12px) contrast(120%) opacity(.8);
				filter: blur(12px) contrast(120%) opacity(.8);
			}
			header {
				position: relative;
				display: block;
				.icon {
					padding: .5em;
					text-transform: uppercase;
					font-size: .65em;
					letter-spacing: 0.1em;
					color: #fff;
					text-decoration: none;
					cursor: pointer;
					.icon__item {
						display: inline-block;
						background-position: 1px 3px;
						background-repeat: no-repeat;
						background-image: $icon-arrow;
						width: 1.5em;
						height: 1em;
						@include opacity(.5);
						@include transition(all .3s ease);
					}
					.icon__text{
						@include opacity(.8);
						@include transition(all .3s ease);
					}
					&:hover {
						.icon__item, .icon__text {
							@include opacity(1);
						}
					}
				}
			}
			article {
				position: relative;
				z-index: 1;
				display: block;
				form {				
					position: relative;
					z-index: 1;
					display: block;
					padding: 1em 5em;
					.form__item {
						width: 100%;
						margin-bottom: 1em;
						text-align: center;
						&:last-child {
							margin-bottom: 0;
						}
						.form__input {
							position: relative;
							text-align: center;
							font-size: 18px;
							padding: .6em 4em;
							border-radius: 25px;
							border: 1px solid rgba(255,255,255,.5);
							background-color: transparent;
							opacity: .6;
							color: #fff;
							@include transition(all .5s ease);
							&.no_true {
								&.no {
									opacity: 1;
									background-color: #fff;
									color: #b40000;
									background-image: $icon-no-true;
									background-repeat: no-repeat;
									background-position: center right 1em;
									background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
									background-position: center right .5em;
									background-size: calc(1em + 1rem) calc(1em + 1rem);
								}
							}
							&.true {
								opacity: 1;
								&.input__phone {
									background-image: $icon-true;
									background-repeat: no-repeat;
									background-position: center right 1em;
									background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
								}
							}
							&:focus {
								outline: none;
								opacity: 1;
								background-color: #fff;
								color: initial;
								&.no {
									color: initial;
									background-image: none;
								}
							}
							&::placeholder {
								color: #fff;
							}
							&:-ms-input-placeholder { /* Internet Explorer 10-11 */ 
								color: #ffffff;
							} 
							&::-ms-input-placeholder { /* Microsoft Edge */ 
								color: #ffffff; 
							}
							&.input__button {
								text-transform: uppercase;
								font-size: .65em;
								letter-spacing: 0.1em;
								cursor: pointer;
								font-weight: bold;
								padding: 1.6em 5em;
								&.active {
									opacity: 1;
									&:hover {
										background-color: #fff;
										color: initial;
										opacity: 1;
									}
								}
								&.disabled {
									cursor: default;
								}
							}
						}
						.form__group {
							width: 49%;
							min-width: 10em;
							display: inline-block;
							text-align: center;
							.form-group__checkbox{
								display: inline-block;
								height: 22px;
								width: 44px;
								border-radius: 1em;
								position: relative;
								cursor: pointer;
								overflow: hidden;
								border: 1px solid #fff;
								outline: none;
								margin-right: .5em;
								.checkbox__input{
									visibility: hidden;
									&:focus{
										outline: none;
									} 
									&:checked ~ .checkbox__dot{
										background: #fff;
										transform: translateX(75%);
									}
								}
								.checkbox__dot {
									content: '';
									height: 16px;
									width: 22px;
									position: absolute;
									top: 3px;
									left: 3px;
									z-index: 2;
									background: rgba(255,255,255,.5);
									border-radius: 16px;
									transform: translateX(0);
									@include transition(all .75s cubic-bezier(.0,.97,.97,.97));
								}
							}
							.form-group__super {
								vertical-align: super;
								font-size: 14px;
							}
							.form-group__password{
								a {
									color: #fff;
									text-decoration: none;
									border-bottom: 1px solid #ccc;
									cursor: pointer;
								}
							}
						}
					}
				}
			}
		}
	}
}


@media all and (max-width: 620px) {
	body {
		main {
			section {
				margin: 1em;
				article {
					form {
						padding: 0;					
					}
				}
			}
		}
	}
}
@media all and (max-width: 460px) {
	body {
		main {
			section {
				padding: 1em;
				article {
					form {
						.form__item {
							.form__input {
								padding: .6em;
								width: 90%;
								&.input__button {
									width: initial;
								}
							}
							.form__group:first-child {
								margin-bottom: 1em;
							}
						}				
					}
				}
			}
		}
	}
}


